<template>
  <transition name="report">
    <div class="report" v-if="openedReport">
      <ControlPanel :show="openedReport" :closeFunc="closeReport"></ControlPanel>
      <div class="report__content">
        <div class="report__header">
          <h4 class="report__title"></h4>
          <ButtonCommon @click="printTable('table-materials')"> </ButtonCommon>
        </div>
        <div class="divider"></div>
        <table class="striped centered table-materials">
          <thead>
            <tr>
              <th> </th>
              <th> ( ), </th>
              <th>,</th>
              <th> , </th>
              <th>    (), </th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="(materialRow, index) in materialList" :key="index">
              <td v-for="(materialItem, index2) in materialRow" :key="index2">{{materialItem}}</td>
            </tr>
          </tbody>
        </table>
        <div class="report__header">
          <h4 class="report__title mt-30"></h4>
          <ButtonCommon @click="printTable('table-labor')"> </ButtonCommon>
        </div>
        <div class="divider"></div>
        <table class="striped centered table-labor">
          <thead>
            <tr>
              <th> </th>
              <th>, .</th>
              <th> .</th>
              <th>L    , </th>
              <th>  </th>
              <th>  </th>
              <th> /, 2</th>
              <th> , </th>
              <th> , </th>
              <th> , </th>
              <th> , </th>
              <th> , </th>
              <th> /, </th>
              <th>, , </th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="(laborRow, index) in laborCosts" :key="index">
              <td v-for="(laborItem, index2) in laborRow" :key="index2">{{laborItem}}</td>
            </tr>
          </tbody>
        </table>
        <FieldInput
          class="mt-30"
          :label="'   '"
          :value="laborHours"
          :disabled="true"
        ></FieldInput>
        <FieldInput
          :label="'    '"
          :value="laborDays"
          :disabled="true"
        ></FieldInput>
      </div>
    </div>
  </transition>
</template>

<script>
import ControlPanel from "./control-panel";
import FieldInput from "./library-details/field-input";
import ButtonCommon from "./button-cummon";

export default {
  components: { FieldInput, ControlPanel, ButtonCommon },
  mounted() {
    bus.$on("openReport", this.openReport);
  },
  methods: {
    printTable(table) {
      let prtContent = document.querySelector(`.${table}`);
      //prettier-ignore
      let WinPrint = window.open("", "", "left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0");

      WinPrint.document.write(`${prtContent.outerHTML}`);
      //prettier-ignore
      WinPrint.document.write("<script src='/public/js/print.js'><\/script>");
      WinPrint.document.close();
    },
    getMaterialList() {
      fetchADV("/technologist/project/compute/materials", {}, data => {
        this.materialList = data;
      });
    },

    getLaborCosts() {
      fetchADV("/technologist/project/compute/labor-costs", {}, data => {
        this.laborHours = data.laborHours;
        this.laborDays = data.laborDays;
        this.laborCosts = data.laborCosts;
      });
    },
    async openReport() {
      await this.getLaborCosts();
      await this.getMaterialList();
      this.openedReport = true;
    },
    closeReport() {
      this.openedReport = false;
    }
  },
  data() {
    return {
      materialList: [],
      laborCosts: [],
      laborHours: 0,
      laborDays: 0,
      openedReport: false
    };
  }
};
</script>

<style lang="scss">
.report {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: white;
  z-index: 100;

  &__header {
    padding-bottom: 1rem;
    display: flex;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
  }

  &__content {
    height: calc(100% - 30px);
    padding: 40px;
    overflow: scroll;
  }
}

.report-enter,
.report-leave-to {
  transform: scale(0);
}

.report-enter-active,
.report-leave-active {
  transition: 0.5s;
}

